/*CSS TWEAKS FOR THE MOBILE*/
@media (max-width: 720px){
  nav {
    width: 100%;
    text-align: center;
  }

  #headerTitle{
    position: relative;
    left: 50%;
    transform: translateX(-50%);
  }

  .gamePage, .winnerPage, .infoPage{
    min-width: 90%;
  }
}

/*CSS FOR THE PC*/
body{
  font: 15px/1.5 Arial, Helvetica,sans-serif;
  padding:0;
  margin:0;
  height: 100vh;
}

.container{
  width: 80%;
  margin: auto;
  overflow: hidden;
}

ul{
  margin:0;
  padding:0;
}

h1{
  font-size: 40px
}

p,li{
  font-size: 20px
}

/*GRADIENT ANIMATION BACKGROUND*/
body{
  background: linear-gradient(270deg, #e24e42, #e24e42, #c492b1, #bce7fd, #12eaea, #b0db43, #b0db43, #12eaea, #bce7fd, #c492b1, #e24e42, #e24e42);
  background-size: 2400% 2400%;
  -webkit-animation: backgroundGradient 40s ease infinite;
  animation: backgroundGradient 40s ease infinite;
  }

  @-webkit-keyframes backgroundGradient {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
  }
  
  @keyframes backgroundGradient { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
  }

/* HEADER */
header{
  background: #E24E42;
  color:#ffffff;
  border-bottom:#ffffff 4px solid;
  padding-top:30px;
  min-height:70px;
}

header a{
  color:#ffffff;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
}

header img{
  float: left;
  margin-right:10px; 
}

header li{
  display:inline;
  font-size: 15px;
  padding: 0 20px 0 20px;
}

header #headerTitle{
  float:left;
}

header #headerTitle h1{
  margin:0;
  font-size: 190%;
}

header nav{
  float:right;
  margin-top:10px;
}

header a:hover{
  color:#000000;
}

/*PAGE CONTAINER*/
.pageContainer{
  text-align: center;
}

/*Common layouts*/
.gamePage, .winnerPage, .infoPage{
  background-color: #ffffff;
  display: inline-block;
  text-align: center;
  outline: none;
  border: none;
  border-radius: 10px;
  padding: 10px;
  margin-top: 20vh;
  width: 50%;
  margin-bottom: 6vh;
}

/*GAME PAGE*/
#congrats{
  font-size: 200%
}

  /*THE GAME BUTTON*/
  .gameButton{
    color: #ffffff;
    background-color: #E24E42;
    cursor: pointer;
    text-align: center;
    font-weight: bold;
    font-size: 200%;
    outline: none;
    border: none;
    border-radius: 10px;
    box-shadow: 0 10px #A9A9A9;
  }

  .gameButton:hover{
    background-color: #cc4439;
  }

  .gameButton:active{
    background-color: #cc4439;
    transform: translateY(5px);
    box-shadow: 0 5px #7c7c7c;
  }

  /*WINNER FORM*/
  form button{
    color: #ffffff;
    background-color: #E24E42;
    text-align: center;
    font-weight: bold;
    outline: none;
    border: none;
    border-radius: 10px;
    box-shadow: 0 5px #A9A9A9;
    padding: 10px;
    margin-left: 4px;
    font-size: 20px;
    margin-top: 4px;
  }

  form button:hover{
    background-color: #cc4439;
  }

  form button:active{
    background-color: #cc4439;
    transform: translateY(2px);
    box-shadow: 0 2px #7c7c7c;
  }

  form input{
    font-size: 20px;
    height: 30px;
    width: 100%;
    border: 2px solid #E24E42;
    border-radius: 4px;
    outline: none;
  }

  .submitButtonDisabled{
    opacity: 0.6;
    cursor: not-allowed;
  }

  .submitButtonShow{
    cursor: pointer;
  }

/*WINNER PAGE*/
.winnerPage li{
  list-style-type: none;
}

  /*LOADING SPINNER*/
  .loadingSpinner {
    margin: 100px auto;
    font-size: 25px;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    position: relative;
    text-indent: -9999em;
    -webkit-animation: spinnerAnim 1.1s infinite ease;
    animation: spinnerAnim 1.1s infinite ease;
